header {
  display: flex;
  justify-content: space-between; }

.menu {
  display: flex;
  width: 50px;
  height: 50px;
  background-color: #362f2f;
  justify-content: center;
  align-items: center;
  i {
    color: white;
    font-size: 28px; } }

.logo {
  position: absolute;
  font-size: 0;
  left: 46.8%;
  top: 0;
  z-index: 1; }

.socials {
  display: flex;
  a {
    color: #362f2f;
    margin-right: 15%;
    transition: color .2s;
    &:hover {
      color: #C2A9A9;
      transition: color .2s; }
    i {
      font-size: 12px; } } }

@media screen and (max-width: 1280px) {
  .logo {
    left: 45.9%; } }

@media screen and (max-width: 960px) {
  .logo {
    left: 44.65%; } }

@media screen and (max-width: 600px) {
  .socials {
    flex-wrap: wrap; }

  .logo {
    left: 41.7%; } }

@media screen and (max-width: 480px) {
  .socials {
    flex-wrap: wrap; }

  .logo {
    left: 39%; } }





